export const roles = {
  citizen: 'citizen',
  mafia: 'mafia',
  don: 'don',
  sheriff: 'sheriff',
  city: 'city',
};

export const isMafia = role => [roles.don, roles.mafia].includes(role);

export const translateRole = role => {
  switch (role) {
    case roles.mafia: return '';
    case roles.citizen: return '';
    case roles.don: return '';
    case roles.sheriff: return '';
    case roles.city: return '';
    default: return '';
  }
}